@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-light.woff2") format("woff2"), url("../fonts/bloggersans-light.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 300;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-lightitalic.woff2") format("woff2"), url("../fonts/bloggersans-lightitalic.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans.woff2") format("woff2"), url("../fonts/bloggersans.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 400;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-italic.woff2") format("woff2"), url("../fonts/bloggersans-italic.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-medium.woff2") format("woff2"), url("../fonts/bloggersans-medium.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 500;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-mediumitalic.woff2") format("woff2"), url("../fonts/bloggersans-mediumitalic.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-bold.woff2") format("woff2"), url("../fonts/bloggersans-bold.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 700;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-bolditalic.woff2") format("woff2"), url("../fonts/bloggersans-bolditalic.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  color: #FFFFFF;
  display: flex;
  margin: 0;
  flex-direction: column;
  min-width: 320px;
  background-color: #161C35;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  body {
    font-size: 28px;
    line-height: 24px;
  }
}

.page-form {
  color: #444444;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  min-width: 320px;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1440px) {
  .container {
    margin: 0 auto;
    max-width: 1330px;
  }
}

.section-description {
  padding: 0 25px;
}

.section-description__text {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 300;
}

.section-description__text--bold {
  margin-bottom: 0;
  font-weight: 700;
}

.text-wrapper {
  padding: 0 25px;
}
@media (min-width: 768px) {
  .text-wrapper {
    padding: 0 50px;
  }
}
@media (min-width: 1440px) {
  .text-wrapper {
    margin: 0 auto;
    max-width: 1330px;
  }
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.user-list {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.user-list__link {
  position: relative;
  display: block;
  width: 40px;
  height: 40px;
  text-decoration: none;
  color: inherit;
  border-radius: 50%;
}

.user-list__link--tel {
  background-color: #FF8D30;
  background-image: url("../img/icon-call.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.user-list__link--email {
  background-color: #FFBD30;
  background-image: url("../img/icon-top-menu-mail.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.links-list {
  padding-top: 46px;
  padding-bottom: 51px;
}

.links-list__link {
  font-size: 30px;
  line-height: 35px;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
  color: #192144;
}

.top-menu__container {
  background-color: #161C35;
  color: #FFFFFF;
}

.top-menu .top-menu__container {
  background-color: #FFFFFF;
}
.top-menu .main-nav__logo path {
  fill: #000000;
}
.top-menu .main-nav__toggle {
  display: none;
}
.top-menu .main-nav {
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.top-menu .main-nav__wrapper {
  display: block;
  position: static;
}
.top-menu .contacts-list {
  margin-bottom: 41px;
}
.top-menu .contacts-list__link {
  text-align: left;
  color: #192144;
}
.top-menu .link-button {
  margin-bottom: 51px;
}

.top-menu--scroll {
  position: fixed;
  z-index: 20;
}
.top-menu--scroll .top-menu__container {
  background-color: #FFFFFF;
}
.top-menu--scroll .main-nav__logo path {
  fill: #000000;
}
.top-menu--scroll .main-nav__toggle:after {
  top: 8px;
  transform: rotate(45deg);
  background-color: #CCCCCC;
}
.top-menu--scroll .main-nav__toggle::before {
  top: 8px;
  transform: rotate(-45deg);
  background-color: #CCCCCC;
}

.top-menu--open {
  background-color: #FFFFFF;
}
.top-menu--open .main-nav {
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.top-menu--open .main-nav__wrapper {
  display: flex;
  position: absolute;
}
.top-menu--open .main-nav__toggle {
  display: block;
}
.top-menu--open .main-nav__toggle:after {
  top: 2px;
  transform: rotate(45deg);
  background-color: #CCCCCC;
}
.top-menu--open .main-nav__toggle::before {
  top: 2px;
  transform: rotate(-45deg);
  background-color: #CCCCCC;
}
.top-menu--open .main-nav__wrapper {
  top: 72px;
}

.top-menu--close .top-menu__container {
  background-color: #161C35;
}
.top-menu--close .main-nav__logo path {
  fill: #FFFFFF;
}
.top-menu--close .main-nav {
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.top-menu--close .main-nav__wrapper {
  display: flex;
  position: absolute;
}
.top-menu--close .main-nav__toggle {
  display: block;
}
.top-menu--close .main-nav__toggle:after {
  display: block;
  top: 2px;
  width: 25px;
  height: 3px;
  content: "";
}
.top-menu--close .main-nav__toggle::before {
  display: block;
  top: 9px;
  width: 25px;
  height: 3px;
  content: "";
}
.top-menu--close .main-nav__wrapper {
  top: -500px;
}

.social-list {
  display: flex;
  justify-content: center;
  min-width: 135px;
}

.social-list__item:not(:last-child) {
  margin-right: 26px;
}

.social-list__link {
  display: block;
  width: 23px;
  height: 21px;
}
.social-list__link--fb {
  background: url("../img/icon_facebook.svg") 50% 50% no-repeat;
}
.social-list__link--vk {
  background: url("../img/icon_vkontakte.svg") 50% 50% no-repeat;
}
.social-list__link--ig {
  background: url("../../img/icon_instagram.svg") 50% 50% no-repeat;
}

.contacts-list__link {
  position: relative;
  display: block;
  padding-top: 12px;
  padding-bottom: 11px;
  padding-left: 61px;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #FFD74B;
}
.contacts-list__link::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 45px;
  height: 45px;
  background-color: #121936;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  border-radius: 50%;
  content: "";
}
.contacts-list__link--tel::after {
  background-image: url("../img/icon-telephone.svg");
}
.contacts-list__link--mail::after {
  background-image: url("../img/icon-mail.svg");
}

.main-nav {
  display: flex;
  justify-content: space-between;
  padding: 26px 0 25px 0;
}

.main-nav__wrapper {
  position: absolute;
  left: 0;
  top: -400px;
  display: flex;
  padding: 0 20px;
  padding-bottom: 41px;
  text-align: center;
  flex-direction: column;
  width: 100%;
  background-color: #FFFFFF;
  z-index: 5;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}

.main-nav__toggle {
  position: relative;
  width: 25px;
  height: 11px;
  border: none;
  background-color: transparent;
}
.main-nav__toggle:after {
  position: absolute;
  top: 6px;
  left: 0;
  width: 25px;
  height: 3px;
  background-color: #FFFFFF;
  border-radius: 2px;
  content: "";
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.main-nav__toggle::before {
  position: absolute;
  top: 14px;
  left: 0;
  width: 25px;
  height: 3px;
  background-color: #FFFFFF;
  border-radius: 2px;
  content: "";
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}

.promo {
  padding-top: 21px;
  padding-bottom: 39px;
  background-color: #161C35;
}

.promo__container {
  padding: 0;
  background-image: url("../img/globe-bg.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-clip: content-box;
  background-position-y: 180px;
}

.promo__heading {
  margin: 0;
  font-size: 36px;
  line-height: 40px;
  text-align: center;
  color: #FFFFFF;
}

.promo__quote {
  margin-left: 25px;
}

.promo__review {
  margin: 0;
  margin-bottom: 18px;
  max-width: 153px;
  font-style: italic;
  font-weight: 300;
}

.promo__author {
  display: block;
  max-width: 161px;
}

.promo__description {
  display: block;
  margin-bottom: 30px;
}

.promo__list {
  counter-reset: numbers;
}

.promo__item {
  position: relative;
  padding: 22px 140px 25px 24px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  background-color: #FF8D30;
  color: #161C35;
}
.promo__item:after {
  position: absolute;
  right: 10px;
  top: 10px;
  color: #FFFFFF;
  counter-increment: numbers;
  content: counter(numbers);
}
.promo__item--step-two {
  background-color: #FFA530;
  padding-right: 96px;
}
.promo__item--step-three {
  background-color: #FFBD30;
  padding-right: 108px;
}
.promo__item--step-four {
  background-color: #FFD74B;
  padding-right: 121px;
}

.promo__item:not(:first-child) {
  margin-top: -12px;
}

.promo__item:last-child {
  border-radius: 15px;
  padding-bottom: 14px;
}

.promo__list--user {
  position: relative;
  padding-top: 245px;
  z-index: 0;
}
.promo__list--user::after {
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  width: 192px;
  height: 255px;
  background: url("../../img/promo-list-pseudo.png");
  content: "";
}

.link-button {
  display: block;
  padding: 9px 40px;
  font-size: 18px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: #1D2E5B;
  background-color: #FFD74B;
  border-radius: 25px;
}

.link-button--showall {
  position: relative;
  max-width: 270px;
  margin: 0 auto;
  padding: 20px 36px 20px 23px;
  text-align: left;
  border-radius: 10px;
  text-transform: none;
}
.link-button--showall::after {
  position: absolute;
  top: 24px;
  right: 24px;
  width: 13px;
  height: 13px;
  background: url("../img/arrow-right.svg") no-repeat center;
  content: "";
}

.about {
  position: relative;
  padding-top: 33px;
  padding-bottom: 82px;
  margin-top: -1px;
  background-color: #161C35;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}

.about__container {
  background-image: url("../img/about-mobile.png");
  background-repeat: no-repeat;
  background-position-x: 20px;
}

.about__button {
  display: none;
}

.heading {
  margin: 0;
  margin-bottom: 20px;
  padding: 0 25px;
  font-size: 24px;
  line-height: 24px;
  color: #FF8D30;
}
.heading--inner {
  margin-bottom: 29px;
  padding: 0;
  font-size: 20px;
  font-weight: 700;
  color: #1D2E5B;
}

.directions {
  margin-top: -20px;
  padding-top: 70px;
  padding-bottom: 34px;
  background-color: #EDEFF6;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.directions__text {
  font-weight: 300;
  color: #444444;
  margin-bottom: 60px;
}

.directions__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin-bottom: 27px;
}

.card {
  font-weight: 500;
  color: #444444;
}

.card__image {
  width: 100%;
  object-fit: cover;
  display: block;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.card__description {
  position: relative;
  display: grid;
  margin-top: -10px;
  padding-top: 25px;
  padding-bottom: 25px;
  grid-template-columns: 155px 67px;
  background-color: #FFFFFF;
  border-radius: 15px;
  z-index: 1;
}

.card__heading {
  margin: 0;
  font-size: 21px;
  line-height: 21px;
  color: #000000;
}

.card__subheading {
  grid-row: 2;
  margin: 0;
  line-height: 18px;
  opacity: 0.3;
}

.card__people-counter {
  position: relative;
  margin: 0;
  padding-left: 20px;
  line-height: 20px;
  opacity: 0.3;
}
.card__people-counter::before {
  position: absolute;
  top: 2px;
  left: 0;
  width: 14px;
  height: 14px;
  background: url("../img/icon-user.svg");
  content: "";
}

.companions {
  position: relative;
  padding-top: 68px;
  padding-bottom: 31px;
  background-color: #161C35;
  border-radius: 0 0 15px 15px;
}

.companions__heading {
  position: relative;
  z-index: 1;
}
.companions__heading:after {
  position: absolute;
  top: -34px;
  left: -24px;
  width: 113px;
  height: 132px;
  background-color: #121936;
  border-radius: 20px;
  z-index: -1;
  content: "";
}
.companions__heading::before {
  position: absolute;
  top: -34px;
  right: 0;
  width: 143px;
  height: 143px;
  background: url("../img/companions-text-back.jpg");
  border-radius: 20px;
  z-index: -1;
  content: "";
}

.companions__description {
  margin-bottom: 58px;
  position: relative;
  z-index: 1;
}

.companions__list {
  position: relative;
  max-width: 270px;
  margin: 0 auto 20px auto;
}
.companions__list::before {
  position: absolute;
  top: -34px;
  left: -25px;
  width: 64px;
  height: 71px;
  background: url("../img/companions-card-back.png");
  border-radius: 20px;
  content: "";
}

.companions__item:not(:last-child) {
  margin-bottom: 9px;
}

.companions-card {
  max-width: 270px;
}

.companions-card picture {
  display: block;
}

.companions-card__image {
  position: relative;
  display: block;
  border-radius: 10px;
}

.companions-card__description {
  position: relative;
  display: flex;
  margin-top: -10px;
  padding: 27px 40px;
  flex-direction: column;
  text-align: center;
  background-color: #FFFFFF;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.companions-card__list {
  display: flex;
  margin-bottom: 14px;
  max-width: 188px;
  justify-content: center;
  flex-wrap: wrap;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
}

.companions-card__item:not(:last-child) {
  margin-right: 5px;
}

.companions-card__name {
  position: relative;
  margin: 0;
  margin-bottom: 15px;
  font-size: 21px;
  line-height: 21px;
  font-weight: 700;
  color: #161C35;
}
.companions-card__name::before {
  position: absolute;
  top: calc(50% - 3px);
  left: 10px;
  width: 6px;
  height: 6px;
  background-color: #81E31E;
  border-radius: 50%;
  content: "";
}
.companions-card__name--offline::before {
  background-color: #F47E7E;
}

.companions-card__item:not(.companions-card__item--active) svg {
  opacity: 0.15;
}

.flags-list {
  position: absolute;
  top: -10px;
  display: flex;
  width: 142px;
  left: calc(50% - 71px);
  justify-content: space-evenly;
}

.transport-list {
  margin-bottom: 0;
}

.transport-list .companions-card__item:not(:last-child) {
  margin-right: 13px;
}

.level-mark {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  top: 10px;
  left: 10px;
  font-size: 14px;
  line-height: 14px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 50%;
}

.level-mark__underscore {
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.level-mark__ring {
  position: absolute;
}

.level-mark__circle {
  stroke-dashoffset: 4;
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}

.level-mark--catalog {
  position: static;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -10px;
  width: 45px;
  height: 45px;
  background-color: transparent;
}
.level-mark--catalog .level-mark__ring {
  stroke-linecap: round;
}

.add-profile {
  position: relative;
  margin-top: -20px;
  padding-top: 70px;
  padding-bottom: 34px;
  background-color: #EDEFF6;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  color: #444444;
}

.add-profile__heading {
  min-height: 62px;
  margin: 0 100px 43px 0;
}

.add-profile__link {
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  text-decoration: none;
  color: #132576;
}

.modal {
  position: absolute;
  display: none;
  flex-direction: column;
  top: -5px;
  left: calc(50% - 134px);
  padding: 0;
  padding-top: 34px;
  background-color: #FFFFFF;
  border-radius: 20px;
  box-shadow: 0px 2px 8px rgba(29, 46, 91, 0.14);
}
.modal--open {
  display: flex;
}

.modal__heading {
  margin: 0;
  margin-bottom: 21px;
  color: #FF8D30;
  text-align: center;
}

.modal__button {
  padding: 21px 0;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  text-transform: uppercase;
  color: #1D2E5B;
  background-color: #F3F4F8;
  border: none;
  border-radius: 0 0 20px 20px;
}

.rate-table {
  position: relative;
  width: 100%;
  margin-bottom: 30px;
  counter-reset: numbers;
}
.rate-table::after {
  position: absolute;
  top: -130px;
  right: 0;
  width: 100px;
  height: 100px;
  background: url("../img/rates.png");
  content: "";
}

.rate-table__row {
  position: relative;
  display: flex;
  min-width: 245px;
  padding: 14px 0 15px;
  flex-direction: column;
  font-size: 16px;
  line-height: 16px;
  font-weight: 300;
}
.rate-table__row::after {
  position: absolute;
  top: 8px;
  right: 0;
  width: 34px;
  height: 34px;
  background-color: #FF8D30;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #FFFFFF;
  counter-increment: numbers;
  content: counter(numbers);
}
.rate-table__row--second::after {
  background-color: #FFBD30;
}
.rate-table__row--third::after {
  background-color: #FFD74B;
}

.rate-table--modal {
  margin-bottom: 32px;
  padding: 0 25px;
  border-collapse: collapse;
  border-top: 1px solid #F2F2F2;
  border-bottom: 1px solid #F2F2F2;
}
.rate-table--modal::after {
  display: none;
}
.rate-table--modal .rate-table__row {
  min-width: 220px;
}
.rate-table--modal .rate-table__row:not(:last-child) {
  border-bottom: 1px solid #F2F2F2;
}
